ального решения. Такой тщательный отбор позволяет нам быть уверенными, что мы нашли «своего» человека.\n          Если хотите присоединиться к нашей команде, посмотрите вакансии.\n        ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row",staticStyle:{"margin-bottom":"16rem"}},[_c('div',{staticClass:"col-4"},[_c('img',{attrs:{"src":require("../assets/images/2.jpg"),"width":"100%","srcset":require("../assets/images/2@2x.jpg") + " 2x, " + require("../assets/images/2@3x.jpg") + " 3x, " + "","alt":""}})]),_c('div',{staticClass:"col-4"},[_c('img',{attrs:{"src":require("../assets/images/3.jpg"),"width":"100%","srcset":require("../assets/images/3@2x.jpg") + " 2x, " + require("../assets/images/3@3x.jpg") + " 3x, " + "","alt":""}})]),_c('div',{staticClass:"col-4"},[_c('img',{attrs:{"src":require("../assets/images/4.jpg"),"width":"100%","srcset":require("../assets/images/4@2x.jpg") + " 2x, " + require("../assets/images/4@3x.jpg") + " 3x, " + "","alt":""}})])])}]

